<template>
  <div>
    <van-sticky>
      <van-nav-bar class="bar_sty" left-text="职位列表">
        <template #right>
          <van-icon @click="jobStateOpen" name="plus" size="18" />
          <van-icon name="search" size="18" />
        </template>
      </van-nav-bar>
      <div class="title_menu">
        <van-button
          class="miniBtn"
          @click="notifyFnBtn"
          type="default"
          size="mini"
          >推荐</van-button
        >
        <van-button
          class="miniBtn"
          @click="notifyFnBtn"
          type="default"
          size="mini"
          >最新</van-button
        >
        <van-button
          class="filterBtn"
          type="default"
          size="mini"
          @click="openMaskFn"
          >{{ defaultCity }}</van-button
        >
        <van-button
          class="filterBtn"
          type="default"
          size="mini"
          @click="openFilterMaskFn"
          >筛选</van-button
        >
      </div>
      <!-- 提示组件 -->
      <notify v-show="notifyshow"></notify>
    </van-sticky>
    <!-- 职位列表 -->
    <div>
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <job_item
          v-for="(n, inx) in jobsData"
          :key="inx"
          :itemObj="n"
        ></job_item>
      </van-pull-refresh>
    </div>

    <!-- 城市操作 -->
    <div
      v-show="is_cityMaskObj"
      :class="maskClassName"
      @touchmove.prevent
      @mousewheel.prevent
    >
      <selectArea @closeFn="hideMaskFn($event)"></selectArea>
    </div>

    <!-- 筛选组件 -->
    <div
      v-show="is_filter"
      :class="filterClassName"
      @touchmove.prevent
      @mousewheel.prevent
    >
      <filterwrObj @closeFilterFn="closeFilterMask"></filterwrObj>
    </div>

    <!-- 管理求职意向 -->
    <div
      v-show="is_jobState"
      :class="jobStateClassName"
      @touchmove.prevent
      @mousewheel.prevent
    >
      <jobState @closeMaskFn="jobStateClose"></jobState>
    </div>
    <!-- footerbar -->
    <footerbar></footerbar>
  </div>
</template>

<script>
import job_item from "./job_item.vue";
import footerbar from "./footerbar.vue";
import notify from "./notify.vue";
import selectArea from "./selectArea.vue";
import filterwrObj from "./filterwrObj.vue";
import jobState from "./jobState.vue";
import { Toast } from "vant";

export default {
  name: "index_jobList",
  data() {
    return {
      msg: "职位列表",
      jobsData: [
        {
          title: "web前端",
          h2_txt: "什么祥 未融资",
          area: ["沈阳 和平区", "经验不限", "本科"],
          hr: {
            img_avatar: "",
            hr_txt: "海峰-招聘者",
          },
          salary: "5-6K",
        },
        {
          title: "软件开发工程师",
          h2_txt: "牛万科技 未融资",
          area: ["沈阳 沈河区", "1-3年", "学历不限"],
          hr: {
            img_avatar: "",
            hr_txt: "海峰-招聘者",
          },
          salary: "3-8K",
        },
        {
          title: "前端工程师",
          h2_txt: "成林健康科技 未融资",
          area: ["沈阳 皇姑区", "经验不限", "本科"],
          hr: {
            img_avatar: "",
            hr_txt: "马xx-从事招聘专员",
          },
          salary: "5-6K",
        },
        {
          title: "前端开发工程师",
          h2_txt: "师福教育 未融资",
          area: ["沈阳 沈河区", "3-5年", "学历不限"],
          hr: {
            img_avatar: "",
            hr_txt: "杨女士 - 人事",
          },
          salary: "5-8K",
        },
        {
          title: "前端工程师",
          h2_txt: "成林健康科技 未融资",
          area: ["沈阳 皇姑区", "经验不限", "本科"],
          hr: {
            img_avatar: "",
            hr_txt: "马xx-从事招聘专员",
          },
          salary: "5-6K",
        },
        {
          title: "前端技术经理",
          h2_txt: "星擎科技 未融资",
          area: ["沈阳 铁西区", "5-10年", "本科"],
          hr: {
            img_avatar: "",
            hr_txt: "潘舒-CEO",
          },
          salary: "6-11K",
        },
        {
          title: "web前端",
          h2_txt: "什么祥 未融资",
          area: ["沈阳 和平区", "经验不限", "本科"],
          hr: {
            img_avatar: "",
            hr_txt: "海峰-招聘者",
          },
          salary: "5-6K",
        },
      ],
      notifyshow: false,
      isLoading: false,
      is_cityMaskObj: false,
      is_filter: false,
      is_jobState: false,
      //   mask_aaaa的用途，仅用于js能找到这个div，没有其他用途，空的css
      maskClassName: "mask_aaaa",
      //   mask_filter的用途，仅用于js能找到这个div，没有其他用途,空的css
      filterClassName: "mask_filter",
      //   mask_animation的用途，仅用于js能找到这个div，没有其他用途,空的css
      jobStateClassName: "mask_animation",
      defaultCity: "无锡",
    };
  },
  components: {
    job_item,
    footerbar,
    notify,
    selectArea,
    filterwrObj,
    jobState,
  },
  methods: {
    notifyFnBtn() {
      this.notifyshow = true;
      //要判断，当css3动画执行结束时，再怎么样
      let _notify = document.querySelector(".notify");
      _notify.addEventListener("webkitAnimationEnd", () => {
        this.notifyshow = false;
      });
    },

    onRefresh() {
      //   console.log(123);
      Toast("刷新成功");
      this.isLoading = false;
    },
    // 城市切换打开，先切为true，再等css3动画结束之后重置为true
    // 否则，将响应 hideMaskFn 函数的css3事件监听
    openMaskFn() {
      this.is_cityMaskObj = true;
      this.maskClassName = "mask_aaaa cityEditWrapObj_open";

      // css3动画运行结束之后，
      // console.log(_rect)
      let _rect = document.querySelector(".mask_aaaa");
      _rect.addEventListener("webkitAnimationEnd", () => {
        this.is_cityMaskObj = true;
      });
    },
    // 关闭城市选择面板
    hideMaskFn(_cityName) {
      if (!_cityName) {
        this.defaultCity = "无锡";
      } else {
        this.defaultCity = _cityName;
      }
      this.maskClassName = "mask_aaaa cityEditWrapObj_close";

      // css3动画运行结束之后，
      // console.log(_rect)
      let _rect = document.querySelector(".mask_aaaa");
      _rect.addEventListener("webkitAnimationEnd", () => {
        this.is_cityMaskObj = false;
      });
    },

    // 打开筛选
    openFilterMaskFn() {
      this.is_filter = true;
      this.filterClassName = "mask_filter cityEditWrapObj_open";

      // css3动画运行结束之后，
      // console.log(_rect)
      let _rect = document.querySelector(".mask_filter");
      _rect.addEventListener("webkitAnimationEnd", () => {
        this.is_filter = true;
      });
    },

    // 关闭筛选
    closeFilterMask() {
      this.filterClassName = "mask_filter filter_close";
      // css3动画运行结束之后，
      let _rect = document.querySelector(".mask_filter");
      // console.log(_rect)
      _rect.addEventListener(
        "webkitAnimationEnd",
        () => {
          this.is_filter = false;
        },
        false
      );
    },
    // 求职状态管理
    jobStateOpen() {
      this.is_jobState = true;
      this.jobStateClassName = "mask_animation animation_open ";
      // css3动画运行结束之后，
      // console.log(_rect)
      let _rect = document.querySelector(".mask_animation");
      _rect.addEventListener("webkitAnimationEnd", () => {
        this.is_jobState = true;
      });
    },
    jobStateClose() {
      this.jobStateClassName = "mask_animation animation_close";
      // css3动画运行结束之后，
      let _rect = document.querySelector(".mask_animation");
      // console.log(_rect)
      _rect.addEventListener(
        "webkitAnimationEnd",
        () => {
          this.is_jobState = false;
        },
        false
      );
    },
  },
};
</script>

<style scoped>
.bar_sty {
  background: #14c1bb;
}
.van-icon-plus:before {
  color: #fff;
  font-size: 0.4rem;
  margin-right: 0.1rem;
  border-right: 1px solid #c1c1c1;
  padding-right: 0.1rem;
}
.van-icon-search:before {
  color: #fff;
  font-size: 0.4rem;
}
.van-nav-bar__text {
  font-size: 0.35rem;
  color: #fff;
}
.miniBtn {
  border: 0;
  margin: 0.1rem 0 0 0;
}
.filterBtn {
  float: right;
  border: 0;
  margin: 0.1rem 0.1rem 0 0;
  background: #f2f3f5;
}
.title_menu {
  background: #fff;
}
.cityEditWrapObj_open {
  width: 100%;
  height: 100%;
  background: #fff;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 3333;
  animation: cityEditWrapObjAnimation_open 0.7s;
}
.cityEditWrapObj_close {
  width: 100%;
  height: 100%;
  background: #fff;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 3333;
  animation: cityEditWrapObjAnimation_close 0.7s;
}
@keyframes cityEditWrapObjAnimation_open {
  from {
    height: 0;
  }
  to {
    height: 100%;
  }
}
@keyframes cityEditWrapObjAnimation_close {
  from {
    height: 100%;
  }
  to {
    height: 0;
  }
}
.filter_open {
  width: 100%;
  height: 100%;
  background: #fff;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 3333;
  animation: cityEditWrapObjAnimation_open 0.7s;
}
.filter_close {
  width: 100%;
  height: 100%;
  background: #fff;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 3333;
  animation: cityEditWrapObjAnimation_close 0.7s;
}
@keyframes cityEditWrapObjAnimation_open {
  from {
    height: 0;
  }
  to {
    height: 100%;
  }
}
@keyframes cityEditWrapObjAnimation_close {
  from {
    height: 100%;
  }
  to {
    height: 0;
  }
}
.mask_animation {
  width: 100%;
  height: 100%;
  background: #fff;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 3333;
}
.animation_open {
  animation: open_Animation 0.7s;
}
.animation_close {
  animation: close_Animation 0.7s;
}

@keyframes open_Animation {
  from {
    width: 0;
    left: 100%;
  }
  to {
    width: 100%;
    left: 0;
  }
}
@keyframes close_Animation {
  from {
    width: 100%;
    left: 0;
  }
  to {
    width: 0;
    left: 100%;
  }
}
</style>



