<template>
  <div>
    <van-area
      :title="title_name"
      :area-list="areaList"
      :visible-item-count="8"
      @change="changeItemFn"
      @cancel="closeFn"
      @confirm="selectAreaBtnFn"
    />
  </div>
</template>

<script>
import { areaList } from "@vant/area-data";

export default {
  name: "selectArea",
  data() {
    return {
      msg: "选择城区组件",
      areaList,
      title_name: "请选择省市区",
    };
  },
  methods: {
    //   改变城市选项时触发，它会默认传递你选择的东西，通过log查看其数据结构
    changeItemFn(d) {
      console.log(d.getValues());
      let _data = d.getValues();
      this.title_name =
        _data[0].name + "-" + _data[1].name + "-" + _data[2].name;
    },
    closeFn() {
      this.$emit("closeFn");
    },
    // 确认按钮按钮
    selectAreaBtnFn(_d) {
      this.$emit("closeFn", _d[1].name);
    },
  },
};
</script>



